body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  padding: 10px;
  background-color: firebrick;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.header{
  font-size: 10px;
  padding: 0;
  margin: 0;
  text-align: center;
}

.map{
  width: 100%;
  height: 100vh;
  position: relative;
}

.wildfire-icon{
  font-size: 2rem;
  color: yellow;
}

.volcano-icon{
  font-size: 2rem;
  color: red;
}

.loader{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader img{
  width: 300px;
}

.location-info{
  position: absolute;
  top: 80px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0,0,0,0.6);
  color: #fff;
  border-radius: 10px;
  font-size: 18px;
}

.location-info ul{
  list-style: none;
  padding: 0;
}

.location-info li{
  padding: 5px 0;
}

